        <!-- ÈáçË§áÁïôË®ÄÂêçÂñÆ -->
        <div v-if="duplicates.length" class="duplicates-list">
          <h2 class="font-semibold mb-2 text-red-600">
            ÈáçË§áÁïôË®ÄÂ∏≥ËôüÔºà{{ duplicates.length }} ‰ΩçÔºâ
          </h2>
          <ul class="list-disc pl-5 text-sm text-gray-700">
            <li v-for="(item, idx) in duplicates" :key="idx">
              {{ item.account }} - ÁïôË®Ä {{ item.count }} Ê¨°Ôºà
              {{ item.numbers.map(n => 'Êé®' + n).join(', ') }}
              Ôºâ
            </li>
          </ul>
        </div>

        <!-- ÂèÉÂä†ÂêçÂñÆ -->
        <div v-if="parsed.length" class="participants-list">
          <h2 class="font-semibold mb-2">ÂèÉÂä†ÂêçÂñÆÔºà{{ parsed.length }}‰∫∫ÔºâÔºö</h2>
          <ul class="list-disc pl-5">
            <li v-for="(entry, i) in parsed" :key="i">
